/* Общие */
@font-face {
  font-family: "Hyundai Sans Head Office";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/HyundaiSansHeadOffice.woff2") format("woff2"), url("../fonts/HyundaiSansHeadOffice-Regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Hyundai Sans Head Office";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/HyundaiSansHeadOffice-Medium.woff2") format("woff2"), url("../fonts/HyundaiSansHeadOffice-Medium.woff") format("woff");
  font-display: swap; }

html {
  font-family: "Hyundai Sans Head Office", "Arial", sans-serif;
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  font-weight: normal;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden,
input[type="radio"] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Блоки */
.container {
  width: 100%;
  max-width: 1660px;
  padding: 0 130px;
  margin: 0 auto; }
  @media (max-width: 1024px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 480px) {
    .container {
      padding: 0 24px; } }

.button {
  padding: 0;
  background-color: transparent;
  border: none; }
